.nvd3 .nv-background {
  fill: #ccc;
}
.nvd3 .nv-axis .tick line {
  opacity: 0;
  display: none;
}
.nvd3 .nv-axis.nv-y text {
  display: none;
}
.nvd3 .nv-axis.nv-y path {
  stroke: #afafaf;
}
.nvd3 .nv-axis.nv-x path.domain {
  stroke-opacity: 1;
  stroke: #afafaf;
}
.nvd3 .nv-axis text {
  fill: #afafaf;
}
