a.md-button ng-md-icon {
  position: relative;
  top: 0.5em;
}
.md-button .md-ripple-container {
  border-radius: 20px;
}
.md-button.small {
  min-width: 0;
  min-height: 0;
}
.md-button.md-icon-button.small {
  min-width: 0;
  min-height: 0;
  width: 24px;
  height: 24px;
  line-height: 24px;
}
.md-button.md-icon-button.small ng-md-icon {
  top: 5px;
}
.md-button.md-lowspace {
  padding: 0 10px;
}
.md-button.md-border {
  border-width: 1px !important;
}
.md-button.md-border:focus {
  border-width: 1px;
}
.md-button.md-fab.md-mini .material-icons {
  margin-left: -2px;
  margin-top: -2px;
}
a.md-button span {
  margin: 0 0 0 5px;
}
md-list-item {
  color: #333333;
}
md-list-item path {
  fill: #afafaf;
}
md-list-item .md-button {
  border-radius: 0;
}
md-list-item .md-button .md-ripple-container {
  border-radius: 0;
}
.md-border {
  border: 1px solid !important;
}
.md-border.md-focused {
  border: 1px solid;
}
md-input-container.md-has-icon {
  padding-left: 36px;
}
md-input-container ng-md-icon {
  position: absolute;
  top: 5px;
  left: 2px;
}
md-menu-content {
  padding: 0;
}
.md-open-menu-container md-menu-divider {
  margin: 0;
}
ng-md-icon path {
  fill: #006bb9;
}
ng-md-icon.no-color path {
  fill: #d7dddd;
}
md-checkbox.no-label .md-label {
  margin-left: 0;
}
md-checkbox.no-space {
  margin: 0;
  padding: 0;
}
md-toolbar.clean {
  color: #afafaf !important;
  background-color: #fff !important;
  padding-top: 1em;
}
md-toolbar.clean .md-toolbar-tools {
  font-size: 1.2em;
}
.info.success,
p.success,
span.success {
  color: #00ccaa;
}
.info.success path,
p.success path,
span.success path {
  fill: #00ccaa;
}
.info.warning,
p.warning,
span.warning {
  color: #f44336;
}
.info.warning path,
p.warning path,
span.warning path {
  fill: #f44336;
}
.mini-info {
  max-width: 305px;
  text-align: justify;
  color: #aaa;
  font-size: 0.9em;
}
.md-warn ng-md-icon path {
  fill: #f44336;
}
md-sidenav {
  box-sizing: border-box;
  position: absolute;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  z-index: 60;
  bottom: 0;
  overflow: auto;
}
md-sidenav#sidebar.md-locked-open {
  width: 370px;
  min-width: 370px;
  max-width: 370px;
}
@media screen and (max-width: 959px) {
  md-sidenav#sidebar {
    min-width: 100%;
    max-width: 100%;
    width: 100%;
  }
  md-sidenav#sidebar.md-closed {
    transform: translate3d(0, calc(100% - 106px), 0);
    display: flex;
    min-width: 100%;
    max-width: 100%;
    width: 100%;
    overflow: visible;
  }
  md-sidenav#sidebar.md-closed > md-content {
    overflow: visible;
  }
  md-sidenav#sidebar.md-closed .menu-toggler {
    display: none;
  }
  md-sidenav#sidebar.md-closed .back-button {
    display: none;
  }
  md-sidenav#sidebar.md-closed .hideOnClosed {
    display: none;
  }
  md-sidenav#sidebar.md-closed .tinyOnClosed {
    font-size: 0.9em;
  }
  md-sidenav#sidebar.md-closed .tinyOnClosed.title {
    margin-top: 0.8em;
    margin-bottom: 0;
  }
  md-sidenav#sidebar.md-closed .noPaddingOnClosed {
    padding: 0 1em !important;
    border: 0;
  }
  md-sidenav#sidebar.md-closed .noPaddingOnClosed > div {
    border: 0;
  }
  md-sidenav#sidebar.md-closed .noPaddingOnClosed::after {
    border: 0;
  }
  md-sidenav#sidebar.md-closed .pullToTopOnClosed {
    position: fixed;
    top: calc(-100% + 102px + 1em);
  }
  md-sidenav#sidebar.md-closed .scaleSmallOnClosed {
    transform: scale(0.7);
    transform-origin: top left;
  }
  md-sidenav#sidebar.md-closed .scaleSmallOnClosed.scaleCenter {
    transform-origin: center left;
  }
  md-sidenav#sidebar.md-closed .scaleSmallOnClosed.scaleRight {
    transform-origin: center right;
  }
  md-sidenav#sidebar.md-closed .portUpOnClosed {
    margin-top: -0.3em;
    margin-left: 0.3em;
  }
  md-sidenav#sidebar.md-closed section.header {
    padding-top: 0.5em;
  }
  md-sidenav#sidebar.md-closed section.header img {
    max-height: 40px;
  }
}
md-sidenav#sidemenu {
  z-index: 61;
  width: 48px;
  min-width: 48px;
  max-width: 48px;
  border-right: 1px solid #d7dddd;
}
md-sidenav#sidemenu md-list md-list-item {
  border: 0;
  padding-left: 0;
  min-width: 48px;
  cursor: pointer;
  /*&.active{
            background-color:@base;
            color:#fff;
            md-icon{
              color:#fff;
            }
          }*/
}
md-sidenav#sidemenu md-list md-list-item .md-no-style.md-button {
  padding-left: 0;
  min-width: 48px;
}
md-sidenav#sidemenu md-list md-list-item > md-icon:first-child:not(.md-avatar-icon) {
  margin: 11px;
}
md-sidenav#sidemenu md-list .md-list-item-inner > md-icon:first-child:not(.md-avatar-icon) {
  margin: 11px;
}
md-sidenav#sidemenu md-list .md-button {
  margin: 0;
  padding: 0;
  min-width: 48px;
  min-height: 48px;
}
.md-subheader .md-subheader-inner {
  font-size: 20px;
  line-height: 1.1em;
}
.md-top {
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 20;
  padding: 0 1em;
  background-color: #fff;
  height: 100%;
}
.md-top md-input-container .md-errors-spacer {
  position: absolute;
}
.md-subheader-wrapper:not(.md-sticky-no-effect).md-sticky-clone {
  z-index: 21;
}
md-bottom-sheet {
  position: fixed;
}
md-tooltip {
  font-size: 0.7em;
  font-family: 'Roboto';
  font-weight: 100;
  white-space: wrap;
  text-overflow: clip;
}
md-tooltip .md-content {
  white-space: normal;
  text-overflow: inherit;
  background-color: #006cb9;
}
.noselect {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Chrome/Safari/Opera */
  -khtml-user-select: none;
  /* Konqueror */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* IE/Edge */
  user-select: none;
  /* non-prefixed version, currently
                                  not supported by any browser */
}
md-input-container .md-errors-spacer {
  position: absolute;
}
[md-color-picker] .md-color-picker-input-container .md-color-picker-clear,
md-color-picker .md-color-picker-input-container .md-color-picker-clear {
  bottom: 16px;
}
