.split-layout .md-subheader-content {
  text-align: center;
}
.split-layout .md-subheader-content h4 {
  margin: 0px;
}
.split-layout .rowed .md-subheader-content {
  text-align: left;
  display: flex;
  text-align: center;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  align-content: center;
}
.toggled.closed {
  overflow: hidden;
  height: 0px;
  padding: 0;
}
