*:focus {
  border: 0;
  outline: 0;
}
body,
html {
  width: 100%;
  height: 100%;
  /*@media screen and (max-width: 600px){
      height: calc(~'100% + 32px');
    }*/
}
html {
  /* enough space to scroll up to get fullscreen on iOS8 */
}
body {
  padding-bottom: 0 !important;
  font-family: 'Gorgia';
  font-size: 16px;
  color: #606060;
  overflow-x: hidden;
  background: url("/images/geometry.png");
}
body > canvas {
  z-index: 1000;
  position: fixed;
  right: 10px;
  bottom: 10px;
}
body > md-content {
  position: relative;
}
body > md-content::before {
  content: '';
  width: 100%;
  left: 0;
  height: 1px;
  position: absolute;
  top: -1px;
  border-bottom: 1px solid #d7dddd;
  z-index: 61;
  transition: all 0.35s ease;
  transition-delay: 0.1s;
}
body.loggedIn > md-content::before {
  width: calc(100% - 47px);
  left: 47px;
}
@media screen and (max-width: 959px) {
  body.loggedIn > md-content::before {
    left: 0;
    width: 100%;
  }
}
body > md-content {
  transition: all 1s ease-in-out;
  opacity: 1;
}
body md-toolbar.Header {
  transform: translateY(0px);
}
body:not(.startup) {
  overflow: hidden;
}
body:not(.startup) md-toolbar.Header {
  transform: translateY(-50px);
  z-index: 10;
}
body:not(.startup) > md-content {
  opacity: 0;
}
.Page-name {
  color: #006bb9;
}
.logo {
  color: #006bb9;
  font-size: 1.5em;
}
.info {
  color: #006bb9;
}
.info path {
  fill: #006bb9;
}
.icon-space {
  position: relative;
  top: 4px;
  left: 5px;
}
.inline-list li {
  display: inline;
}
.inline-list.menu-list li {
  position: relative;
  padding: 0 1em;
}
.inline-list.menu-list li::before {
  content: "/";
  position: absolute;
  right: 0;
  top: 0;
}
.inline-list.menu-list li:last-child::before {
  content: "";
}
.inline-list.menu-list li.active a {
  color: #006bb9;
}
.inline-list.menu-list li a {
  position: relative;
  color: #333333;
}
.inline-list.menu-list li a::after {
  content: '';
  position: absolute;
  height: 1px;
  width: 0;
  left: 50%;
  bottom: -5px;
  border-bottom: 1px solid #006bb9;
  transition: all 0.2s ease;
}
.inline-list.menu-list li a:hover::after {
  width: 100%;
  left: 0;
}
#hack {
  z-index: 100000;
  /* should be bigger than everything else */
  position: fixed;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  visibility: hidden;
  height: 0;
}
.icon-bubble {
  background-color: #d7dddd;
  color: #fff;
  border-radius: 50%;
  font-size: 2em;
}
.icon-bubble.fisheries {
  background-color: #008c8c;
}
.icon-bubble.healthimpact {
  background-color: #ff9600;
}
.icon-bubble.airquality {
  background-color: #f7c80b;
}
.icon-bubble.watersanitation {
  background-color: #ff6d24;
}
.icon-bubble.waterresources {
  background-color: #7993f2;
}
.icon-bubble.agriculture {
  background-color: #009bcc;
}
.icon-bubble.forest {
  background-color: #2e74ba;
}
.icon-bubble.biodiversity {
  background-color: #00ccaa;
}
.icon-bubble.climate {
  background-color: #1cb85d;
}
p.small-info {
  color: #aaa;
  font-size: 0.9em;
}
p.small-info:hover {
  color: #006bb9;
}
.lower-text {
  font-size: 0.9em;
}
.rowed #map {
  left: 400px;
  width: calc(100% - 400px);
}
.rowed .Page {
  width: 400px;
  max-width: 400px;
  height: 100%;
}
.loose .Header {
  background: #fff !important;
}
.loose .Page {
  margin-left: calc(370px + 2em);
  margin-top: 1em;
  margin-right: 1em;
  transition: margin-left 0.25s ease-in-out;
  transition-delay: 0s;
  width: auto;
  position: absolute;
  max-height: calc(100% - 2em);
  max-width: calc(100% - 370px - 3em);
  z-index: 2;
}
.loose .Page section.table {
  height: calc(100% - 2em);
}
.loose #sidebar {
  position: absolute !important;
  top: 1em;
  left: 1em;
  bottom: auto;
  max-height: calc(100% - 2em);
  overflow-x: hidden;
}
.loose.sidebar-closed .Page {
  transition-delay: 0.1s;
}
.loose #map {
  left: 0;
  width: 100%;
}
.loose.rowed .Page {
  width: 400px;
}
.loose .additional {
  top: 1em;
  height: calc(100% - 2em);
  left: calc(800px + 1em);
}
.loose.loggedIn #sidebar {
  left: calc(48px + 1em);
}
.loose.fixed .md-sidenav-backdrop {
  display: none;
}
@media screen and (max-width: 959px) {
  .loose.fixed .Header {
    min-height: 0;
    height: 0;
    transition: all 0.2s ease;
  }
  .loose.fixed .Header .customer-logo {
    top: 48px;
    transition: all 0.2s linear;
  }
  .loose.fixed .Header .data-button {
    display: none;
  }
}
.loose.fixed #sidebar {
  top: 0;
  left: 0;
  bottom: auto;
  max-height: 100%;
  overflow-x: hidden;
}
@media screen and (max-width: 959px) {
  .loose.fixed #sidebar {
    top: 0;
    height: 36px;
    transform: translate3d(0, 0, 0);
    overflow-y: visible;
    overflow-x: visible;
  }
  .loose.fixed #sidebar md-content[ui-view='sidebar'] {
    background: transparent;
    overflow: visible;
    height: 0;
  }
  .loose.fixed #sidebar md-content[ui-view='sidebar'] > .selector md-input-container {
    margin: 0;
  }
  .loose.fixed #sidebar .mobile-container {
    transition: all 0.3s ease-in-out;
    transform: translateY(calc(100vh - 320px + 53px));
    background: #fff;
  }
  .loose.fixed #sidebar .mobile-container .comparison {
    min-height: 200px;
    max-height: calc(100vh - 330px);
    overflow-y: auto;
  }
  .loose.fixed #sidebar .mobile-container .country-chooser {
    padding: 0 8px;
  }
}
@media only screen and (max-device-width: 667px) and (-webkit-device-pixel-ratio: 2) {
  .loose.fixed:not(.iframed) .mobile-container {
    transform: translateY(calc(100vh - 395px + 53px));
  }
}
@media screen and (min-device-width: 414px) and (-webkit-device-pixel-ratio: 3) {
  .loose.fixed:not(.iframed) .mobile-container {
    transform: translateY(calc(100vh - 395px + 53px));
  }
}
.loose.fixed.loggedIn #sidebar {
  left: 48px;
}
@media screen and (max-width: 959px) {
  .loose.fixed.loggedIn #sidebar {
    left: 0;
  }
}
.loose.fixed .mobile-window-switcher {
  bottom: 16em;
  display: none;
}
.loose.fixed #map .leaflet-control-home {
  position: fixed;
  top: 58px;
  left: 380px;
}
.loose.fixed #map .leaflet-toggle-label {
  display: none;
}
@media screen and (max-width: 959px) {
  .loose.fixed #map {
    top: 36px;
    height: calc(100% - 267px);
    transition: all 0.2s ease;
  }
  .loose.fixed #map .leaflet-top.leaflet-left {
    display: none;
  }
  .loose.fixed #map .leaflet-control-zoom,
  .loose.fixed #map .leaflet-toggle-label {
    display: none;
  }
  .loose.fixed #map .leaflet-control-home {
    position: fixed;
    top: auto;
    left: 10px;
    bottom: 235px;
    z-index: 4;
    display: block;
    border-radius: 50%;
    transition: all 0.3s ease;
  }
  .loose.fixed #map .leaflet-control-home a {
    border-radius: 50%;
    width: 48px;
    height: 48px;
    line-height: 48px;
  }
  .loose.fixed #map .leaflet-control-home a md-icon {
    font-size: 32px;
    width: 32px;
    height: 32px;
    line-height: 28px;
  }
}
@media screen and (max-width: 959px) {
  .loose.fixed.sidebar-closed .Header {
    min-height: 48px;
    height: 48px;
    overflow: auto;
  }
  .loose.fixed.sidebar-closed .Header .customer-logo {
    top: 0;
    right: 0;
    height: 48px;
    background-position: right center;
  }
  .loose.fixed.sidebar-closed .Header .data-button {
    display: inline-block;
    margin-right: 100px;
  }
}
@media screen and (max-width: 959px) {
  .loose.fixed.sidebar-closed #map {
    height: calc(100% - 167px);
  }
  .loose.fixed.sidebar-closed #map .legend {
    display: block;
  }
  .loose.fixed.sidebar-closed #map .leaflet-control-home {
    bottom: 135px;
  }
}
@media only screen and (max-width: 959px) {
  .loose.fixed.sidebar-closed #sidebar {
    top: 0;
    height: 36px;
    transform: translate3d(0, 0, 0);
    overflow-y: visible;
    overflow-x: visible;
  }
  .loose.fixed.sidebar-closed #sidebar md-content[ui-view='sidebar'] {
    height: 36px;
  }
  .loose.fixed.sidebar-closed #sidebar md-content[ui-view='sidebar'] > .selector md-input-container {
    margin: 0;
  }
  .loose.fixed.sidebar-closed #sidebar .mobile-container {
    transform: translateY(calc(100vh - 215px));
    min-height: 115px;
  }
}
@media only screen and (max-device-width: 667px) and (-webkit-device-pixel-ratio: 2) {
  .loose.fixed.sidebar-closed:not(.iframed) #sidebar .mobile-container {
    transform: translateY(calc(100vh - 285px));
  }
}
@media screen and (min-device-width: 414px) and (-webkit-device-pixel-ratio: 3) {
  .loose.fixed.sidebar-closed:not(.iframed) #sidebar .mobile-container {
    transform: translateY(calc(100vh - 285px));
  }
}
.loose.fixed.sidebar-closed .mobile-window-switcher {
  bottom: 9.5em;
}
.loose.fixed.sidebar-closed .mobile-container {
  transition: all 0.3s ease-in-out;
  box-shadow: 0 1px 8px 0 rgba(0, 0, 0, 0.2), 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 2px 1px -1px rgba(0, 0, 0, 0.12);
}
.mobile-container {
  padding-bottom: 1em;
}
