.additional {
  position: relative;
  z-index: 1;
  width: 400px;
  height: 100%;
  border-left: 1px solid #ddd;
}
.additional > div:first-child,
.additional md-content:first-child {
  pointer-events: auto;
}
.addFull .additional {
  width: 100%;
  height: auto;
}
.addFull #map {
  top: 355px;
  height: calc(100% - 355px);
}
.noHeader .Header {
  display: none;
}
.menu-toggler {
  position: absolute;
  width: 100%;
  height: 102px;
  z-index: 61;
  cursor: pointer;
  display: none;
}
.mobile-window-switcher {
  position: fixed;
  bottom: 0.5em;
  right: 0.2em;
  z-index: 61;
  transition: bottom 0.5s ease;
}
.mobile-window-switcher path {
  fill: #fff;
}
.sidebar-closed .mobile-window-switcher {
  bottom: 4.2em;
  right: 0.2em;
}
.columned {
  -webkit-column-count: 3;
  /* Chrome, Safari, Opera */
  -moz-column-count: 3;
  /* Firefox */
  column-count: 3;
}
.social-buttons {
  margin-top: 0.5em;
  color: #004288;
  width: 100px;
  display: none;
}
.social-buttons a {
  text-decoration: none;
  color: #004288;
  margin-bottom: 5px;
}
.social-buttons a i {
  font-size: 1.5em;
}
#fullscreen-view {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 61;
  background: #fff;
  min-height: 400px;
}
