.indicator-tabs .indicator-fields {
  padding: 0 1em;
  max-width: 500px;
  margin: 0 auto;
}
.indicator-tabs md-tab-content md-content {
  padding-bottom: 1em;
  padding-top: 1em;
}
.indicator-tabs md-tab-item ng-md-icon:not(.valid) path {
  fill: #f44336;
}
.indicator-tabs md-tabs > md-tabs-wrapper > md-tabs-canvas > md-pagination-wrapper > md-tab-item {
  background: #efefef;
}
.indicator-tabs md-tabs > md-tabs-wrapper > md-tabs-canvas > md-pagination-wrapper > md-tab-item.md-active {
  background: #fff;
}
.indicator-tabs .md-tab.md-disabled {
  opacity: 1;
}
.indicator-tabs .md-tab.md-disabled ng-md-icon {
  opacity: 0.3;
}
.indicator-tabs md-autocomplete[md-floating-label] md-input-container {
  padding-bottom: 2px;
}
