color-gradient md-input-container {
  display: none;
}
color-gradient .picker-wrapper {
  position: relative;
  padding: 0 2em;
  min-height: 35px;
}
color-gradient .picker-wrapper .picker-container {
  position: absolute;
}
color-gradient .picker-wrapper .picker-container .gradient-color-picker {
  position: relative;
  left: -12px;
}
color-gradient .picker-wrapper .picker-container .gradient-color-picker .md-color-picker-clear {
  display: none;
}
color-gradient .picker-wrapper .picker-container .close {
  opacity: 0;
  position: absolute;
  transition: all 0.3s ease;
  top: 40px;
  left: 10px;
  cursor: pointer;
  font-size: 18px;
}
color-gradient .picker-wrapper .picker-container .drag-handle {
  opacity: 0;
  position: absolute;
  top: 9px;
  left: -12px;
  cursor: ew-resize;
}
color-gradient .picker-wrapper .picker-container .drag-handle md-icon {
  font-size: 18px;
}
color-gradient .picker-wrapper .picker-container:hover .close {
  opacity: 1;
}
color-gradient .picker-wrapper .picker-container:hover .drag-handle {
  opacity: 1;
}
