.chapter-image {
  position: relative;
  min-height: 175px;
  overflow: hidden;
}
.chapter-image .image {
  max-width: 100%;
  width: 100%;
  display: inherit;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  background-size: cover;
  background-position: center center;
}
.chapter-image h1,
.chapter-image h2,
.chapter-image h3,
.chapter-image h4,
.chapter-image p,
.chapter-image span {
  position: relative;
  z-index: 1;
  color: #fff;
  text-shadow: 0 0 3px #000000;
}
.chapter-image h2 {
  margin: 0.8em 0;
  font-size: 1.2em;
}
.chapter-image h2::before {
  content: '';
  width: 26px;
  border-top: 1px solid;
  position: absolute;
  top: -5px;
  left: 50%;
  margin-left: -13px;
}
.chapter-image md-input-container {
  margin: 0;
  font-size: 1.3em;
  text-shadow: 0 0 3px #000000;
}
.chapter-image md-input-container .md-select-icon {
  font-size: 12px;
}
.chapter-image md-input-container .md-select-value {
  border-bottom-color: rgba(255, 255, 255, 0.12);
}
.chapter-image.dark h1,
.chapter-image.dark h2,
.chapter-image.dark h3,
.chapter-image.dark h4,
.chapter-image.dark p,
.chapter-image.dark span {
  color: #333;
  text-shadow: 1px 0 0 rgba(255, 255, 255, 0.5);
}
.chapter-image.dark md-input-container {
  text-shadow: 0 0 3px #ffffff;
}
.chapter-image.dark md-input-container .md-select-value {
  border-bottom-color: rgba(0, 0, 0, 0.12);
}
.md-subheader.selector .md-subheader-inner {
  padding-top: 0;
  padding-bottom: 0;
}
.md-subheader.selector .md-subheader-inner md-input-container {
  margin-top: 12px;
}
.hint {
  padding: 0.5em 1em;
  border-radius: 20px;
  text-transform: lowercase;
  border-width: 0;
  font-weight: bold;
  margin: 1em 0 0 1em;
}
