.thumb-selector {
  padding: 0;
  margin: 0px;
  text-align: center;
  white-space: nowrap;
  overflow: auto;
}
.thumb-selector li {
  display: inline-block;
  list-style-type: none;
  margin: .5em;
  transition: all 0.2s ease-in;
  outline: 2px solid #fff;
}
.thumb-selector li.active {
  outline: 2px solid #006bb9;
}
.mini-map .leaflet-control-container {
  display: none !important;
}
