bubbles {
  overflow: hidden;
  margin: 1em auto;
}
.node {
  cursor: pointer;
}
.tooltip {
  color: #333;
  position: fixed;
  padding: 1em;
  background-color: #ffffff;
  border: 1px solid #d7dddd;
  z-index: 99;
  min-width: 250px;
}
.tooltip::after,
.tooltip::before {
  right: 100%;
  top: 50%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}
.tooltip::after {
  border-right-color: #fff;
  border-width: 10px;
  margin-top: -10px;
}
.tooltip::before {
  border-right-color: #d7dddd;
  border-width: 11px;
  margin-top: -11px;
}
.tooltip .close-it {
  position: absolute;
  top: 2px;
  right: 4px;
  cursor: pointer;
  color: #d7dddd;
}
.tooltip .close-it:hover {
  color: rgba(0, 0, 0, 0.87);
}
.tooltip .title {
  margin: 0.3em 0;
  margin-bottom: 1em;
  font-size: 1.1em;
}
.tooltip .sub {
  margin-left: 2em;
  margin-top: 1em;
}
.tooltip .sub .name {
  margin: 0.3em 0;
}
.tooltip span {
  display: inline-block;
}
.tooltip md-progress-linear .md-container .md-bar2 {
  transform: translateX(-100%) scale(0, 1);
  transition: transform 0.3s ease-out;
}
