.doAnim.ng-enter {
  -webkit-animation: slideInRight 0.5s;
  -moz-animation: slideInRight 0.5s;
  -ms-animation: slideInRight 0.5s;
  animation: slideInRight 0.5s;
}
.doAnim.ng-leave {
  -webkit-animation: slideOutRight 0.3s;
  -moz-animation: slideOutRight 0.3s;
  -ms-animation: slideOutRight 0.3s;
  animation: slideOutRight 0.3s;
  position: absolute;
  top: 0;
  width: 100%;
}
.doAnim-right.ng-enter {
  -webkit-animation: slideInLeft .5s;
  -moz-animation: slideInLeft .5s;
  -ms-animation: slideInLeft .5s;
  animation: slideInLeft .5s;
  animation-delay: 0.1s;
  z-index: 2;
}
.doAnim-right.ng-leave {
  -webkit-animation: slideOutRight .5s;
  -moz-animation: slideOutRight .5s;
  -ms-animation: slideOutRight .5s;
  animation: slideOutRight .5s;
  position: absolute;
  animation-delay: 0.1s;
  z-index: 1;
  top: 0;
}
.doAnim-hinge.ng-enter {
  -webkit-animation: bounceInDown 0.5s;
  -moz-animation: bounceInDown 0.5s;
  -ms-animation: bounceInDown 0.5s;
  animation: bounceInDown 0.5s;
  z-index: 1;
}
.doAnim-hinge.ng-leave {
  -webkit-animation: bounceOutDown 0.75s;
  -moz-animation: bounceOutDown 0.75s;
  -ms-animation: bounceOutDown 0.75s;
  animation: bounceOutDown 0.75s;
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
}
.doAnim-hinge.ng-leave.additional {
  left: 400px;
}
.addFull .doAnim-hinge.ng-leave.additional {
  width: 100%;
}
.doAnim-bottom.ng-enter {
  -webkit-animation: slideInDown 0.4s;
  -moz-animation: slideInDown 0.4s;
  -ms-animation: slideInDown 0.4s;
  animation: slideInDown 0.4s;
}
.doAnim-bottom.ng-leave {
  -webkit-animation: slideOutUp 0.3s;
  -moz-animation: slideOutUp 0.3s;
  -ms-animation: slideOutUp 0.3s;
  animation: slideOutUp 0.3s;
}
.doAnim-top.ng-enter {
  -webkit-animation: slideInUp 0.4s;
  -moz-animation: slideInUp 0.4s;
  -ms-animation: slideInUp 0.4s;
  animation: slideInUp 0.4s;
}
.doAnim-top.ng-leave {
  -webkit-animation: fadeOutDown 0.3s;
  -moz-animation: fadeOutDown 0.3s;
  -ms-animation: fadeOutDown 0.3s;
  animation: fadeOutDown 0.3s;
}
.doAnim-left.ng-enter {
  -webkit-animation: slideInLeft 0.5s;
  -moz-animation: slideInLeft 0.5s;
  -ms-animation: slideInLeft 0.5s;
  animation: slideInLeft 0.5s;
  z-index: 2;
}
.doAnim-left.ng-leave {
  -webkit-animation: slideOutLeft 0.3s;
  -moz-animation: slideOutLeft 0.3s;
  -ms-animation: slideOutLeft 0.3s;
  animation: slideOutLeft 0.3s;
  position: absolute;
  z-index: 1;
  top: 0;
}
.doAnim-fade.ng-enter {
  -webkit-animation: fadeIn 0.5s;
  -moz-animation: fadeIn 0.5s;
  -ms-animation: fadeIn 0.5s;
  animation: fadeIn 0.5s;
}
.doAnim-fade.ng-leave {
  -webkit-animation: fadeOut 0.1s;
  -moz-animation: fadeOut 0.1s;
  -ms-animation: fadeOut 0.1s;
  animation: fadeOut 0.1s;
}
.doAnim-fade-long.ng-enter {
  -webkit-animation: fadeIn 0.5s;
  -moz-animation: fadeIn 0.5s;
  -ms-animation: fadeIn 0.5s;
  animation: fadeIn 0.5s;
}
.doAnim-fade-long.ng-leave {
  -webkit-animation: fadeOut 1s;
  -moz-animation: fadeOut 1s;
  -ms-animation: fadeOut 1s;
  animation: fadeOut 1s;
}
.list-anim.ng-enter-stagger,
.list-anim.ng-leave-stagger,
.list-anim.ng-move-stagger {
  /* 200ms will be applied between each sucessive enter operation */
  -webkit-transition-delay: 0.02s;
  transition-delay: 0.02s;
}
.list-anim.ng-enter,
.list-anim.ng-leave,
.list-anim.ng-move {
  -webkit-transition: 0.1s linear all;
  transition: 0.1s linear all;
}
.list-anim.ng-leave.ng-leave-active,
.list-anim.ng-enter,
.list-anim.ng-move {
  -webkit-transition: 0.1s linear all;
  transition: 0.1s linear all;
  opacity: 0;
}
.list-anim.ng-leave,
.list-anim.ng-move.ng-move-active,
.list-anim.ng-enter.ng-enter-active {
  opacity: 1;
}
.anim.ng-enter,
.anim.ng-leave {
  transition: all 0.3s ease-in;
}
.anim.ng-enter,
.anim.ng-leave.ng-leave-active {
  transform: translateY(100%);
  opacity: 0;
}
.anim.ng-enter.ng-enter-active {
  transform: translateY(0%);
  opacity: 1;
}
.anim-total.ng-enter,
.anim.ng-leave {
  transition: all 0.3s ease-in;
}
.anim-total.ng-enter,
.anim-total.ng-leave.ng-leave-active {
  transform: translateX(100%);
  opacity: 0;
  position: absolute;
}
.anim-total.ng-enter.ng-enter-active {
  transform: translateX(0%);
  opacity: 1;
}
.anim-left.ng-enter,
.anim-left.ng-leave {
  transition: all 0.3s ease;
}
.anim-left.ng-enter,
.anim-left.ng-leave.ng-leave-active {
  transform: translateX(-100%);
  z-index: -1;
  opacity: 1;
}
.anim-left.ng-enter.ng-enter-active {
  transform: translateX(0%);
  opacity: 1;
  z-index: -1;
}
.anim-opacity.ng-enter,
.anim-opacity.ng-leave {
  transition: all 0.3s ease-in;
}
.anim-opacity.ng-enter,
.anim-opacity.ng-leave.ng-leave-active {
  opacity: 0;
}
.anim-opacity.ng-enter.ng-enter-active {
  opacity: 1;
}
.anim-fade.ng-enter {
  transition: all 0.4s ease-in-out;
}
.anim-fade.ng-leave {
  transition: all 0.5s ease-in;
}
.anim-fade.ng-enter,
.anim-fade.ng-leave.ng-leave-active {
  opacity: 0;
  transform: scale(0.8);
}
.anim-fade.ng-enter.ng-enter-active {
  opacity: 0;
  transform: scale(1.1);
}
.anim-height {
  transition: all 0.5s ease-in;
}
.anim-height.ng-enter,
.anim-height.ng-leave {
  transition: all 0.5s ease-in;
  overflow: hidden;
}
.anim-height.ng-enter,
.anim-height.ng-leave.ng-leave-active {
  max-height: 0;
}
.anim-height.ng-enter.ng-enter-active {
  max-height: 1000px;
}
