.bar-viewer {
  margin: 0 1em;
}
.bar-viewer .bar {
  width: 100%;
  height: 7px;
  background-color: #efefef;
}
.bar-viewer .bar .bar-value {
  height: 100%;
  background-color: #000;
  width: 0;
  transition: width 0.3s ease-out;
}
.bar-viewer .title {
  margin: .5em 0;
  margin-bottom: 1em;
}
.bar-viewer .sub-bar {
  cursor: pointer;
}
.bar-viewer .sub-bar.titled {
  margin-left: 2em;
}
