.Header {
  transition: all 1s ease-in-out;
  min-height: 48px;
  max-height: 48px;
  background-color: #fff !important;
}
.Header .md-toolbar-tools,
.Header md-toolbar {
  height: 48px;
  min-height: 48px;
  padding-left: 12px;
}
.Header h2 {
  max-height: 48px;
}
.Header a {
  color: #006bb9;
}
.Header a.md-button span {
  color: #006bb9;
}
.Header .md-menu {
  padding: 0px;
}
.Header .inline-list.menu-list li a,
.Header .inline-list.menu-list li::before {
  color: #006bb9;
}
.Header .logo {
  font-size: 3em;
}
md-toolbar.Header .md-button.md-border {
  color: #006bb9;
}
