.country-chooser .md-select-value *:first-child {
  font-size: 1.1em;
  font-weight: bold;
}
.country-chooser md-input-container {
  margin-top: 0;
}
.country-chooser md-input-container.md-input-has-value label {
  display: none;
}
.country-chooser ul[rn-carousel] {
  height: 1.5em;
  margin: 0 1.5em;
}
.country-chooser .rn-carousel-control {
  font-size: 1.3rem;
  top: .1em;
}
.country-chooser .sub-title {
  position: relative;
  font-size: 1em;
  margin-top: 0px;
  text-align: center;
}
@media screen and (max-width: 959px) {
  .country-chooser .mobile-space {
    padding: 0 8px;
    padding-top: 8px;
  }
}
