.split-header {
  position: relative;
  z-index: 1;
  height: 55%;
}
.split-header p {
  position: absolute;
  bottom: 0em;
  right: 1em;
  margin: 0;
  padding: 2em;
  font-size: 1.8em;
  color: #fff;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.3);
  text-align: right;
}
.split-header .exporter-image {
  width: 200px;
  height: 200px;
  position: absolute;
  top: 0px;
  left: 0px;
  background-size: contain;
  background-repeat: no-repeat;
}
@media screen and (max-width: 959px) {
  .split-header .exporter-image {
    left: 50%;
    margin-left: -100px;
  }
}
.split-header .exporter-background {
  background-size: cover;
  background-position: center center;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0px;
  opacity: 0.8;
  background-blend-mode: hard-light;
}
.chapter-container {
  height: 45%;
  box-shadow: 0 0 35px rgba(0, 0, 0, 0.2);
}
.chapter-container .chapter-preview {
  color: #fff;
  text-align: center;
  position: relative;
  cursor: pointer;
  overflow: hidden;
}
.chapter-container .chapter-preview h2 {
  font-size: 1.8em;
  margin-top: 5px;
}
@media screen and (max-width: 959px) {
  .chapter-container .chapter-preview h2 {
    margin: 0;
    margin-bottom: 5px;
    font-size: 1.2em;
  }
}
.chapter-container .chapter-preview p {
  text-align: justify;
  font-weight: 100;
}
.chapter-container .chapter-preview .chapter-info {
  background-color: #ff8000;
  padding: .5em 1em;
  position: relative;
  box-shadow: 0px 0px 35px rgba(0, 0, 0, 0.2);
  transition: all 1s ease-in-out;
}
.chapter-container .chapter-preview .chapter-info .chapter-number {
  position: relative;
  z-index: 1;
  margin: .5em;
  transition: all 2s ease-in-out;
  letter-spacing: 2px;
  text-transform: uppercase;
}
@media screen and (max-width: 959px) {
  .chapter-container .chapter-preview .chapter-info .chapter-number {
    letter-spacing: 1px;
    font-size: 0.8em;
  }
}
.chapter-container .chapter-preview .chapter-info .chapter-title {
  position: relative;
  z-index: 1;
  transition: all 3s ease-in-out;
}
.chapter-container .chapter-preview .chapter-info .chapter-title::before {
  content: '';
  width: 0px;
  position: absolute;
  top: 0px;
  left: 50%;
  margin-left: 0px;
  border-top: 1px solid;
  transition: all 1s ease-in-out;
}
@media screen and (max-width: 959px) {
  .chapter-container .chapter-preview .chapter-info {
    width: 80%;
  }
}
.chapter-container .chapter-preview .chapter-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  transition: all 5s ease-in-out;
  background-position: center center;
}
.chapter-container .chapter-preview:hover .chapter-info {
  background-color: transparent !important;
  box-shadow: 0px 0px 55px rgba(0, 0, 0, 0);
}
@media screen and (min-width: 960px) {
  .chapter-container .chapter-preview:hover .chapter-title {
    letter-spacing: 2px;
  }
  .chapter-container .chapter-preview:hover .chapter-title::before {
    margin-left: -50px;
    width: 100px;
  }
}
.chapter-container .chapter-preview:hover .chapter-image {
  transform: scale(1.2);
}
