.treeview {
  position: relative;
  margin-bottom: .5em;
}
.treeview md-list-item {
  min-height: 32px;
  position: relative;
}
.treeview md-list-item .md-list-item-text > p {
  margin: 0.7em 0;
}
.treeview:not(:first-child) md-list-item {
  padding-right: 0;
}
.treeview .toggler {
  margin: 0 0.3em;
}
.treeview .dndPlaceholder {
  height: 44px;
  list-style-type: none;
  background-color: #efefef;
  border: 1px dashed #ccc;
  margin-left: 1em;
}
.treeview .dndDragging {
  background-color: #006bb9 !important;
  color: #fff;
  width: 100%;
}
.treeview .dndDragging md-icon {
  color: #fff;
}
.treeview .handle {
  cursor: move;
}
.treeview.dndDragover {
  outline: 1px dashed #ccc;
}
.treeview.dndDragover .meanwhile {
  opacity: 0.1;
}
.treeview .styleIcon {
  margin-right: .5em;
}
