image-uploader {
  display: inherit;
  padding: 1em 0;
}
image-uploader .solo-label {
  color: rgba(0, 0, 0, 0.54);
  font-size: .75em;
  text-align: left;
  position: absolute;
  top: -2em;
  left: 3px;
}
.upload-container {
  position: relative;
  width: 100%;
  text-align: center;
  color: #006bb9;
}
.upload-container .material-icons {
  color: #006bb9;
}
.upload-container .image-prev {
  min-height: 200px;
  border: 1px dashed #ccc;
}
.upload-container .image-prev.no-image {
  background-color: #efefef;
}
.upload-container .image-prev .material-icons {
  font-size: 4em;
  width: auto;
  height: auto;
}
.upload-container img {
  max-width: 100%;
}
.upload-container .actions {
  position: absolute;
  top: 1em;
  right: 1em;
}
