compare-countries svg {
  overflow: visible;
}
compare-countries .bar rect {
  fill: steelblue;
}
compare-countries .bar:hover rect {
  fill: brown;
}
compare-countries .value {
  fill: white;
}
compare-countries .axis {
  shape-rendering: crispEdges;
}
compare-countries .axis .tick text {
  fill: #888;
  font-size: 13px;
}
compare-countries .axis path {
  stroke: none;
  display: none;
}
compare-countries .x.axis line {
  stroke: #888;
  stroke-opacity: 0.8;
}
compare-countries .x.axis line.dashed {
  stroke-dasharray: 3,3;
}
compare-countries .y.axis path {
  stroke: black;
}
compare-countries .axis .header {
  font-size: 13px;
  fill: #888;
}
compare-countries .ranks {
  font-style: italic;
  text-anchor: middle;
}
