section.module-top {
  background-color: #fff;
  width: 360px;
  margin-left: 1em;
  margin-top: 1em;
  margin-bottom: 1em;
}
section.module-top.out {
  transform: translateX(-380px);
}
section.module-top .md-subheader {
  font-size: 1.4em;
}
section.module-top .upload-container {
  text-align: center;
}
section.module-top .md-subhead {
  color: #afafaf;
}
section.module-second {
  position: fixed;
  top: 48px;
  left: calc(360px + 1em);
  margin-left: 1em;
  max-height: calc(100% - 48px - 2em);
  min-height: 0;
  height: 100%;
  overflow: hidden;
}
section.module-second .md-subheader-inner {
  padding: 0;
}
section.module-second md-content {
  max-height: calc(100%);
  height: 100%;
}
section.module-second .index-element {
  position: relative;
  width: 100%;
  overflow: hidden;
  transition: all 0.25s ease-in-out;
}
section.module-second .index-element .sub-menue {
  position: absolute;
  right: 0;
  height: 100%;
  transform: translateX(100%);
  transition: all 0.1s ease-in-out;
}
section.module-second .index-element .sub-menue .md-icon-button {
  margin: 0 3px;
}
section.module-second .index-element .sub-menue .md-icon-button ng-md-icon {
  top: 3px;
}
section.module-second .index-element:hover {
  background-color: rgba(0, 0, 0, 0.03);
}
section.module-second .index-element:hover .sub-menue {
  transform: translateX(0%);
}
section.module-second .index-element.editing {
  background-color: #006bb9;
  color: #fff;
}
section.module-second .index-element.editing * path {
  fill: #fff;
}
section.module-third {
  position: fixed;
  top: 48px;
  left: calc(720px + 2em);
  margin-left: 1em;
  max-height: calc(100% - 48px - 2em);
  min-height: 0;
}
section.table {
  background-color: rgba(255, 255, 255, 0.2);
  width: 100%;
  position: relative;
  top: 0;
  left: 0;
  height: calc(100%);
  background: #fff;
}
section.table md-data-table-toolbar {
  position: relative;
  background: #006bb9;
  color: #fff;
}
section.table md-data-table-toolbar * {
  fill: #fff;
}
section.table md-data-table-toolbar md-input-container {
  padding-bottom: 0.5em;
}
section.table md-data-table-container {
  font-family: RobotoDraft, Roboto, 'Helvetica Neue', sans-serif;
  max-height: calc(100% - 120px);
  overflow: auto;
}
section.table md-data-table-container thead:not([md-table-progress]) > tr > th {
  color: #006bb9;
}
section.table md-data-table-container thead:not([md-table-progress]) > tr > th.iconed {
  position: relative;
  padding-left: 3em !important;
}
section.table md-data-table-container thead:not([md-table-progress]) > tr > th.iconed span.icon {
  opacity: 0;
  transition: all 0.3s ease;
  position: absolute;
  top: 1.3em;
  left: 0;
}
section.table md-data-table-container thead:not([md-table-progress]) > tr > th.iconed span.icon path {
  fill: #ccc;
}
section.table md-data-table-container thead:not([md-table-progress]) > tr > th.iconed span.icon:hover path {
  fill: #006bb9;
}
section.table md-data-table-container thead:not([md-table-progress]) > tr > th.iconed.globe span {
  opacity: 1;
}
section.table md-data-table-container thead:not([md-table-progress]) > tr > th.iconed.globe span path {
  fill: #006bb9;
}
section.table md-data-table-container thead:not([md-table-progress]) > tr > th.iconed:hover span.icon {
  opacity: 1;
}
section.table md-data-table-container thead:not([md-table-progress]) > tr > th.done,
section.table md-data-table-container thead:not([md-table-progress]) > tr > th.done.md-active {
  color: green !important;
}
section.table md-data-table-container thead:not([md-table-progress]) > tr > th.done span:hover path,
section.table md-data-table-container thead:not([md-table-progress]) > tr > th.done.md-active span:hover path {
  fill: green !important;
}
section.table md-data-table-container table[md-data-table] > tbody > tr {
  height: 2.5em;
}
section.table md-data-table-container table[md-data-table] > tbody > tr.md-warn {
  background-color: rgba(244, 67, 54, 0.2);
}
section.table md-data-table-container table[md-data-table] > tbody > tr td.md-warn::after {
  content: 'Data missing';
  color: red;
  width: 100%;
  display: inline-block;
  line-height: 3em;
}
section.table md-data-table-pagination {
  bottom: 0;
  width: 100%;
  background: #fff;
}
.angular-ui-tree-placeholder {
  border-left: 3px solid #006bb9;
  max-width: 100%;
  width: 100%;
}
.angular-ui-tree-drag {
  background-color: rgba(255, 255, 255, 0.8);
  width: auto !important;
  padding-right: 1em;
  box-shadow: 0 3px 1px -2px rgba(0, 0, 255, 0.14), 0 2px 2px 0 rgba(0, 0, 255, 0.098), 0 1px 5px 0 rgba(0, 0, 255, 0.084);
}
.angular-ui-tree-drag .actions,
.angular-ui-tree-drag .sub-menue {
  display: none;
}
.angular-ui-tree-node .group {
  color: #006bb9;
  font-size: 1.1em;
}
.angular-ui-tree-node md-checkbox {
  opacity: 0.25;
}
.angular-ui-tree-node md-checkbox.md-checked {
  opacity: 1;
}
.select-list .md-icon-button {
  min-width: 48px;
}
.select-list .icon {
  font-size: 3em;
  position: relative;
  top: 3px;
}
.fill-noheader {
  height: calc(100% - 64px);
  min-height: calc(100% - 64px);
}
md-list md-list-item {
  border-bottom: 1px solid #d7dddd;
}
md-list md-list-item:last-child {
  border-bottom: 0;
}
md-list md-list-item.sub-list {
  padding: 0;
}
md-list md-list-item.sub-list md-list {
  width: 100%;
}
md-list .md-button {
  border-radius: 0;
}
md-list .md-button .md-ripple-container {
  border-radius: 0;
}
.chooser ng-md-icon {
  transition: all .2s ease;
  border: 1px solid rgba(255, 255, 255, 0);
}
.chooser.unchecked > ng-md-icon {
  background-color: #ddd;
}
.chooser.unchecked > ng-md-icon path {
  fill: #fff;
}
.chooser.unchecked.child > ng-md-icon {
  border: 1px solid #20a1ff;
}
.chooser.unchecked.child > ng-md-icon path {
  fill: #20a1ff;
}
.chooser.children > ng-md-icon {
  background-color: #333;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.26);
}
.chooser.children > ng-md-icon path {
  fill: #fff;
}
.chooser.checked > ng-md-icon {
  background-color: #006bb9;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.26);
}
.chooser.checked > ng-md-icon path {
  fill: #fff;
}
.chooser > ng-md-icon {
  top: 0;
  border-radius: 50%;
  width: 22px;
  height: 22px;
  cursor: pointer;
}
.selector {
  border-bottom: 0;
  padding-bottom: 0;
  font-size: 0.95em;
  min-height: 1em;
}
.selector ng-md-icon {
  top: 0;
  border-radius: 50%;
  width: 22px;
  height: 22px;
}
.selector .md-list-item-inner {
  min-height: 1em;
}
.selector.unchecked ng-md-icon {
  background-color: #ddd;
}
.selector.unchecked path {
  fill: #fff;
}
.selector.checked ng-md-icon {
  background-color: #333;
}
.selector.checked path {
  fill: #fff;
}
