.conflict .back-button {
  padding-left: 1em;
  padding-top: 0.5em;
  color: #4fb0e5;
  cursor: pointer;
}
.conflict .back-button ng-md-icon {
  position: relative;
  top: 2px;
}
.conflict .back-button path {
  fill: #4fb0e5;
}
.conflict .back-button:hover {
  animation: shake 10s;
}
.conflict i {
  color: #4fb0e5;
}
.conflict .info-icon {
  cursor: pointer;
}
.conflict .info-icon i {
  color: #d7dddd;
}
.conflict .info-icon:hover i {
  color: #4fb0e5;
}
.conflict .title,
.conflict a,
.conflict h2,
.conflict h3,
.conflict h4,
.conflict small {
  color: #4fb0e5;
  font-weight: normal;
  text-decoration: none;
}
.conflict .title path,
.conflict a path,
.conflict h2 path,
.conflict h3 path,
.conflict h4 path,
.conflict small path {
  fill: #4fb0e5 !important;
}
.conflict .title,
.conflict h3 {
  font-size: 1.3em;
  margin: 0.5em 0;
}
.conflict h3 {
  font-size: 1.2em;
}
.conflict .main-title {
  font-size: 1.5em;
  margin-bottom: 0;
}
.conflict h3 {
  margin-top: 0;
}
.conflict .header > div {
  border-bottom: 2px solid #d7dddd;
}
.conflict .header img {
  height: 50px;
}
.conflict .header .outlink {
  margin-top: 4px;
  font-style: italic;
}
.conflict section {
  padding: 1em 1.5em;
}
.conflict section p {
  margin: 0;
  font-size: 0.95em;
}
.conflict section.inset > div {
  margin-left: 1.5em;
}
.conflict .interstate {
  color: #69d4c3;
}
.conflict .intrastate {
  color: #b7b7b7;
}
.conflict .substate {
  color: #ff9d27;
}
.conflict .info-field {
  font-size: 1.1em;
  margin-bottom: 0.5em;
  cursor: pointer;
  transition: all 0.5s ease;
}
.conflict .info-field .value {
  margin-right: 0.5em;
  min-width: 40px;
}
.conflict .info-field.off {
  opacity: 0.5;
  transform: scale(0.9);
  color: #ccc !important;
}
.conflict .info-text .text {
  height: 85px;
  overflow: hidden;
  transition: all 0.5s ease;
  position: relative;
}
.conflict .info-text .text::after {
  content: '';
  width: 100%;
  height: 2em;
  left: 0;
  bottom: 0;
  position: absolute;
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#ffffff+0,ffffff+100&0+0,1+100 */
  background: -moz-linear-gradient(top, rgba(255, 255, 255, 0) 0%, #ffffff 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0) 0%, #ffffff 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, #ffffff 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00ffffff', endColorstr='#ffffff', GradientType=0);
  /* IE6-9 */
  display: block;
}
.conflict .info-text .text.fullHeight {
  height: 195px;
}
.conflict .info-text .text.fullHeight::after {
  display: none;
}
md-dialog-content p.columned {
  line-height: 1.25em;
  text-align: justify;
}
